import React from 'react';
import { useDispatch } from 'react-redux';

import * as S from './style';
import { setBackgroundColor } from '../../utils/setBackgroundColor';
import { setFilteredPokemon } from '../../redux/pokemonSlice';

const FilteringSection = () => {
  const dispatch = useDispatch();
  const types = [
    'grass',
    'ghost',
    'ice',
    'ground',
    'electric',
    'poison',
    'dragon',
    'normal',
    'steel',
    'flying',
    'fairy',
    'fire',
    'water',
    'bug',
    'dark',
    'rock',
    'psychic',
    'fighting',
  ];

  const filteringPokemon = (e) => {
    const targetType = e.target.textContent;
    dispatch(setFilteredPokemon(targetType));
  };

  return (
    <S.Section>
      {types.map((type) => (
        <S.Button
          key={type}
          type={setBackgroundColor(type)}
          onClick={filteringPokemon}
        >
          {type}
        </S.Button>
      ))}
    </S.Section>
  );
};

export default FilteringSection;
