import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';

import * as S from './style';
import Card from '../Card/Card';
import {
  fetchPokemon,
  fetchPokemonDetails,
  setPokemon,
} from '../../redux/pokemonSlice';

const PokemonCardSection = () => {
  const dispatch = useDispatch();
  const pokemon = useSelector((state) => state.pokemon);
  const [next, setNext] = useState(null);

  const fetchPokemonData = () => {
    dispatch(fetchPokemon(next)).then((res) => {
      const arr = res.payload.results;
      arr.map((item) =>
        dispatch(fetchPokemonDetails(item.url)).then((res) =>
          dispatch(setPokemon(res.payload))
        )
      );
      setNext(res.payload.next);
    });
  };

  useEffect(() => {
      fetchPokemonData();
  }, []);

  const handleMore = () => {
    fetchPokemonData();
  };

  return (
    <>
      <S.PokemonCards>
        {pokemon.map((item) => (
          <Card key={item.name} data={item} />
        ))}
      </S.PokemonCards>
      <S.Button onClick={handleMore}>MORE</S.Button>
    </>
  );
};

export default PokemonCardSection;
