import React from 'react';
import { useNavigate } from 'react-router-dom';
import { useDispatch } from 'react-redux';

import * as S from './style';
import { setBackgroundColor } from '../../utils/setBackgroundColor';
import { fetchPokemonDetails, setDetails } from '../../redux/detailsSlice';

const Card = ({ data}) => {
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const getPokemonDetails = () => {
    const keyword = data.name;
    dispatch(fetchPokemonDetails(keyword)).then((res) => {
      setDetails(res.payload);
      navigate(`/details/${res.payload.name}`);
    });
  };

  return (
    <S.Card
      type={setBackgroundColor(data.types[0].type.name)}
      onClick={getPokemonDetails}
    >
      <S.Images>
        <img src={data.sprites.front_default} alt={data.name} />
        <img src={data.sprites.back_default} alt={data.name} />
      </S.Images>
      <S.Name>{data.name}</S.Name>
    </S.Card>
  );
};

export default Card;
