import React, { useEffect, useRef } from 'react';
import { useNavigate } from 'react-router-dom';
import { useDispatch } from 'react-redux';

import * as S from './style';
import { fetchPokemonDetails, setDetails } from '../../redux/detailsSlice';

const SearchBar = () => {
  const dispatch = useDispatch();
  const inputRef = useRef();
  const navigate = useNavigate();

  useEffect(() => {
    inputRef.current.focus();
  }, []);

  const handleSearch = (e) => {
    if (inputRef.current.value === '') return;
    if (e.key === 'Enter') {
      const keyword = inputRef.current.value;
      dispatch(fetchPokemonDetails(keyword)).then((res) => {
        setDetails(res.payload);
        navigate(`/details/${res.payload.name}`);
      });
    }
  };

  return (
    <S.Input
      type="search"
      placeholder="Input pokemon name or number"
      ref={inputRef}
      onKeyUp={handleSearch}
    />
  );
};

export default SearchBar;
