import { createSlice, createAsyncThunk } from '@reduxjs/toolkit';
import axios from 'axios';

export const fetchPokemon = createAsyncThunk(
  'pokemon/fetchPokemon',
  async (url) => {
    return await axios
      .get(url ?? 'https://pokeapi.co/api/v2/pokemon/')
      .then((res) => res.data);
  }
);

export const fetchPokemonDetails = createAsyncThunk(
  'pokemon/fetchPokemonDetails',
  async (url) => {
    return await axios.get(url).then((res) => res.data);
  }
);

const pokemonSlice = createSlice({
  name: 'pokemon',
  initialState: [],
  reducers: {
    setPokemon: (state, action) => {
      const found = state.find((p) => p.id === action.payload.id);
      if (!found) {
        return [...state, action.payload];
      }
    },
    setFilteredPokemon: (state, action) => {
      const targetType = action.payload;
      return state.filter(
        (p) =>
          p.types[0].type.name === targetType ||
          p.types[1]?.type.name === targetType
      );
    },
  },
  extraReducers: {
    [fetchPokemon.fulfilled]: (state, action) => {
      return action.payload.pokemons;
    },
    [fetchPokemonDetails.fulfilled]: (state) => {
      return state;
    },
  },
});

export const { setPokemon, setFilteredPokemon } = pokemonSlice.actions;
export default pokemonSlice.reducer;
